<template>
  <v-row v-if="gradebook.length > 0">
    <v-col>
      <v-card height="100%">
        <v-card-title>
          GPA
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-icon
                color="primary"
                v-on="on"
                right
              >
                mdi-help-circle
              </v-icon>
            </template>
            <span>GPA is calculated as per the standards specified in the Program of Studies</span>
          </v-tooltip>
          <v-spacer/>
          <v-switch class="mt-0 pt-0" v-model="weighted" @change="getGPA" label="Weighted" hide-details/>
        </v-card-title>
        <v-card-text class="text-center">
          <v-progress-circular v-if="weighted" :value="weightedGPA" :width="8" :size="250" color="primary"><div class="text-h3">{{ weightedGPA.toFixed(2) }}%</div></v-progress-circular>
          <v-progress-circular v-else :value="unweightedGPA" :width="8" :size="250" color="primary"><div class="text-h3">{{ unweightedGPA.toFixed(2) }}%</div></v-progress-circular>
        </v-card-text>
      </v-card>
    </v-col>
    <v-col>
      <v-card height="100%">
        <v-card-title>Next School Day</v-card-title>
        <v-card-text class="text-center">
            <v-list-item class="px-0">
              <v-list-item-content>
                <v-list-item-title v-if="day" class="text-h1">{{ day }} Day</v-list-item-title>
                <v-list-item-subtitle v-if="date" class="text-h5">{{ date }}</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
        </v-card-text>
      </v-card>
    </v-col>
    <v-overlay :value="overlay">
      <v-progress-circular
          indeterminate
          size="64"
      ></v-progress-circular>
    </v-overlay>
  </v-row>
</template>

<script>
import GenesisService from "@/GenesisService";

export default {
  name: "Information",
  data() {
    return {
      gradebook: [],
      histories: [],
      day: "",
      date: "",
      weightedGPA: 0,
      unweightedGPA: 0,
      weightedGPAHistory: [],
      unweightedGPAHistory: [],
      weighted: true,
      overlay: false,
    }
  },
  watch: {
    async "$store.state.studentId"(studentId) {
      await this.getInformation(studentId);
    }
  },
  async created() {
    if (this.$store.state.studentId) await this.getInformation(this.$store.state.studentId);
  },
  methods: {
    async getInformation(studentId) {
      try {
        this.overlay = true;
        this.gradebook = await GenesisService.getGradebook(studentId);
        if (this.gradebook.length > 0) {
          if (this.$store.state.credits.length === 0) this.$store.commit("setCredits", await GenesisService.getCredits(studentId));
          [this.weightedGPA, this.unweightedGPA] = this.getGPA();
          [this.day, this.date] = await GenesisService.getDay(studentId);
        }
        this.overlay = false;
      } catch (error) {
        console.log(error);
      }
    },
    getGPA() {
      let points = 0;
      let total = 0;
      for (let i = 0; i < this.gradebook.length; i++) {
        let grade = parseFloat(this.gradebook[i].grade);
        if (grade) {
          const name = this.gradebook[i].name;
          if (
              name.includes("Honors") ||
              name.endsWith(" H") ||
              name.includes(" H ") ||
              name.includes("H-") ||
              name.includes("Hon") ||
              name.includes("AP")
          )
            grade += 5;
          points += grade * this.$store.state.credits[i];
          total += this.$store.state.credits[i];
        }
      }
      const weightedGPA = points / total;

      points = 0;
      total = 0;
      for (let i = 0; i < this.gradebook.length; i++) {
        const grade = parseFloat(this.gradebook[i].grade);
        if (grade) {
          points += grade * this.$store.state.credits[i];
          total += this.$store.state.credits[i];
        }
      }
      const unweightedGPA = points / total;

      return [weightedGPA, unweightedGPA];
    },
  }
}
</script>
